import * as THREE from 'three'
//
import {
  DeviceOrientationControls,
  OrbitControls
} from '../controls/controls.js'
//FBXLoader
import {
  FBXLoader,
  GLTFLoader
} from '../loaders/loaders.js'
//scene
import {
  scene
} from '../scene/scene.js'
//
import {
  pointLight,
  ambientLight
} from '../lights/lights.js'
//camera
import {
  camera
} from '../camera/camera.js'
//renderer
import {
  renderer
} from '../renderer/renderer.js'

import {
  isMobile
} from '../utils/utils.js'
import {
  Mesh
} from 'three'

window.THREE = THREE
//FBX
const FBXpath1 = process.env.NODE_ENV === 'development' ? './src/mod//ar.gltf' : 'ar.gltf'

//
export function initThree() {
  //
  scene.add(new THREE.AxesHelper(100))
  let mixer1 = null
  let mixer2 = null
  let controls = null
  const clock = new THREE.Clock()
  //FBX
  const loader = new FBXLoader()
  const GLTFloader = new GLTFLoader()
  GLTFloader.load(
    // resource URL
    FBXpath1,
    // called when the resource is loaded
    function (gltf) {

      scene.add(gltf.scene)
      gltf.scene.children[2].children.forEach(v => {
        v.children[0].children[0].visible = false
      })
      mixer1 = new THREE.AnimationMixer(gltf.scene)
      const AnimationAction = mixer1.clipAction(gltf.animations[0])
      AnimationAction.play()
    },
    // called while loading is progressing
    function (xhr) {

      console.log((xhr.loaded / xhr.total * 100) + '% loaded')

    },
    // called when loading has errors
    function (error) {

      console.log('An error happened')

    }
  )

  scene.add(pointLight)
  scene.add(ambientLight)
  //bodycanvas
  document.body.appendChild(renderer.domElement)
  if (isMobile()) {
    //
    controls = new DeviceOrientationControls(camera)
    render(true)
  } else {
    //
    controls = new OrbitControls(camera, renderer.domElement)
    render(false)
  }

  function render(isMobile = false) {
    //render
    requestAnimationFrame(render)
    if(isMobile){
      controls.update()
    }
    renderer.render(scene, camera) //
    if (mixer1 !== null) {
      mixer1.update(clock.getDelta())
    }
  }
}

//
export function initVideo() {
  if (navigator.mediaDevices) {
    navigator.mediaDevices.getUserMedia({
        video: {
          width: document.body.clientWidth,
          height: document.body.clientHeight,
          facingMode: 'environment',
        }
      })
      .then(function (stream) {
        let video = document.querySelector('video')
        video.srcObject = stream
        video.onloadedmetadata = function (e) {
          video.play()
        }
      })
      .catch(function (err) {
        alert('')
      })
  } else {
    navigator.getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia
    if (navigator.getUserMedia) {
      navigator.getUserMedia({
          video: {
            width: document.body.clientWidth,
            height: document.body.clientHeight,
            facingMode: 'environment',
          }
        },
        function (stream) {
          let video = document.querySelector('video')
          video.srcObject = stream
          video.onloadedmetadata = function (e) {
            video.play()
          }
        },
        function (err) {
          alert('')
        }
      )
    } else {
      alert(',')
    }
  }
}